import React, { PureComponent } from "react";
import { isTagEmbed } from "../../../../actions/api";
import { convertToAtob, kFormatter } from '../../../../utils'
const NetworkLike = (networkID, postID, Link) => {
  if (networkID == 1) return `https://twitter.com/intent/favorite?tweet_id=${convertToAtob(postID)}`
  else return Link
}
const NetworkComment = (networkID, postID, Link) => {
  if (networkID == 1) return `https://twitter.com/intent/tweet?in_reply_to=${convertToAtob(postID)}`
  else return Link
}


const SocialItm = ({ arialbl, hrefClick, Icon, count, itemId = 0 }) => {
  return <div className="tb_social_action__list" role="listitem" aria-roledescription="post social action links">
    <div onClick={event => {
      event.stopPropagation()
      window.open(hrefClick, "_blank")
    }} className={`tb_social_action__ico_wrap tb_social_action__ico_wrap_${itemId}`} aria-label={`${arialbl} post,${Icon == "eye" ? `view` : Icon == "heart-outline" ? `like` : Icon == "comment" ? `comment` : ""} count ${kFormatter(count)}`} role="button" tabIndex="0">
      <div className={`tb_social_action_ico__ tb__icon tb-${Icon}`}> </div>
      {count > 0 ? <div className="tb_social_action_counts__">{kFormatter(count)}</div> : ''}
    </div>
  </div>
}
export default class SocialAction extends PureComponent {
  componentDidMount() {
    try {
      const { itemData } = this.props
      const socialActionId = `.tb_social_action__ico_wrap_${itemData?.id}`
      document.querySelector(socialActionId)?.addEventListener('keydown', function (event) {
        if (event.key === 'Enter' || event.key === ' ') {
          event.preventDefault();
          this.click(); // Triggers the existing onClick functionality
        }
      });
    }
    catch (e) {
      console.log(e)
    }
  }
  render() {
    const { itemData, ThemeRule, actionClass, ThemeID } = this.props;

    const iconColorStyle = { color: ThemeID === 3 ? ThemeRule.iconType === 1 ? ThemeRule.iconColor : itemData.network.color : ThemeRule.fontColor }

    const networkID = itemData.network.id
    const likeIcon = (networkID === 3 || networkID === 10 || networkID === 7) ? 'like' : 'heart-outline';
    const likeIconStatus = [1, 2, 3, 7, 8, 9, 10, 18].includes(networkID) ? true : false
    const commentIconStatus = [29, 19].includes(networkID) ? false : true
    const networkName = itemData.network.name ? itemData.network.name : "";
    return <>
      {(networkID !== 29 && networkID !== 26 && networkID !== 23 && networkID !== 6 && networkID !== 5 && networkID !== 11 && networkID !== 12 && networkID !== 15 && networkID !== 20 && networkID !== 21) ?
        <div className={actionClass}>
          <div className="tb_social_action__" role="list">
            {likeIconStatus ? <SocialItm itemId={itemData.id} arialbl={networkName} hrefClick={NetworkLike(networkID, itemData.postId, itemData.link)} Icon={likeIcon} iconColorStyle={iconColorStyle} count={itemData.like_count} /> : null}
            {commentIconStatus ? <SocialItm itemId={itemData.id} arialbl={networkName} hrefClick={NetworkComment(networkID, itemData.postId, itemData.link)} Icon={"comment"} iconColorStyle={iconColorStyle} count={itemData.comment_count} /> : null}
            {networkID === 1 ? <SocialItm itemId={itemData.id} arialbl={networkName} hrefClick={`https://twitter.com/intent/retweet?tweet_id=${convertToAtob(itemData.postId)}`} Icon={"retweet"} iconColorStyle={iconColorStyle} count={itemData.comment_count} /> : null}
            <SocialItm arialbl={networkName} itemId={itemData.id} hrefClick={itemData.link} Icon={"eye"} iconColorStyle={iconColorStyle} count={0} />
          </div>
        </div> : ''}
    </>
  }
}